    <p>
      What you see on the map are plumes of contaminated groundwater spreading
      out from leaking tanks buried on the Central Plateau, or infiltrating from
      the many ditches, pools and pipelines where toxic chemicals were simply
      dumped without any pretense of containment.
    </p>

    <p>
      The plumes, which are artifically separated into distinct bodies by the
      graphic representation, have been identified and tracked by means of
      samples withdrawn by technicians from hundreds of test wells drilled into
      the Hanford Reservation, which is roughly twenty-five miles across from
      east to west.
    </p>

    <p>
      In addition to the groundwater contamination on the Plateau, the reactor
      sites along the Columbia River are marked by plumes of strontium-90. All
      these reactor sites are also surrounded by sinks of hexavalent chromium,
      which was added as an anti-corrosion agent to the cooling water.
      Hexavalent chromium is highly toxic to salmon in particular. It entered
      the river in large quantities during the period of Hanford's active
      operation.
    </p>

    <p>
      Over the last decade, billions of gallons of groundwater have been pumped
      out and filtered in a Herculean attempt to prevent further contamination.
      Yet the plumes persist, and hexavalent chromium still reaches the
      Columbia.
    </p>

    <p>
      On the Central Plateau, a massive Vitrification Plant is being constructed
      in an attempt to seal the tank waste inside glass rods for permanent
      burial. However, the $17-billion-and-counting "Vit plant," underway since
      2002, has been plagued by technical difficulties, delays and cost
      overruns. High-level waste is not expected to be processed into glass
      until 2036&mdash;if ever, according to more skeptical evaluations.
    </p>

    <img src="/images/WasteTank.jpg" class="fullwidth" />
    <div class="caption">Hanford waste tank under construction</div>

    <p>
      Try to imagine the life of a single-shell carbon steel tank, up to one
      million gallons in capacity, welded together on site some fifty to seventy
      years ago, then encased in concrete, buried beneath the desert and
      gradually filled with chemical waste at high levels of radioactivity.
    </p>

    <p>
      Caustic solutions including sodium nitrate, sodium hydroxide, sodium
      aluminate and sodium phosphate are pumped to the tanks from neighboring
      facilities built to extract uranium and plutonium from the spent fuel
      slugs. Further solutions from equipment wash-downs are added to the mix.
      Suspended in the chemical bath are radionuclides including uranium,
      plutonium, strontium-90, cesium-137, americium-241 and technetium-99.
    </p>

    <p>
      Over time, water infiltrates into many of the 149 single-shell tanks,
      while others are subject to sustained high heat generated by the
      chemicals. The steel bubbles, pits and corrodes, while the concrete
      exterior slowly decays. The earliest tanks were designed for a useful life
      of only twenty years. Sixty-seven of the single-shell tanks eventually
      leak.
    </p>

    <p>
      When environmental cleanup finally begins, the liquid component of the
      waste is transferred from the leaking vessels to twenty-eight previously
      constructed double-shell tanks, which themselves were built anywhere from
      thirty to fifty years ago. Transfers are effected through existing
      vertical pipes leading down into the tank, some of them only a foot
      across. Extraordinarily dangerous vapors are released during the transfer
      process, which is done by relatively unprotected workers wrestling with
      pumps, pipes, remote-controlled devices and a plethora of plastic shrouds.
      Two of the supposedly safer double-shell tanks have also leaked. And as
      DOE acknowledges, chemical percipitates and radioactive waste remain
      behind in the drained single-shells:
    </p>

    <div class="quote">
      <p>
        "Whatâ€™s left inside the tanks today are saltcakes, a material with the
