        resemble peanut butter, small broken icebergs, foam, or whitish
        crystals. None of these wastes are easily removed."
      </p>
    </div>

    <img src="/images/TankWorkers.jpg" class="fullwidth" />
    <div class="caption">Tank workers at Hanford's C-farm</div>

    <p>
      Now try to imagine the lives of the workers who are nonetheless charged
      with monitoring the tanks, manipulating the pipes and valves, transferring
      the liquids and in some cases, removing the solids from the bottom of
      damaged units.
    </p>

    <p>
      The first thing to realize is that the empty "headspace" of each tank is
      suffused with toxic vapors from as many as 1,500 chemicals, which must be
      periodically vented to prevent explosions. Workers assigned to tank
      operations, often without air supplies or even masks, suddenly catch one
      bad breath of vapor. Just that breath can cause chemical burns in the
      lungs, followed by lifetime health consequences that may include
      neurological damage. In a frightening spate of events a few years ago,
      over seventy workers received medical examinations for vapor incidents
      during the period from March 2014 to September 2015. Statistics are hard
      to come by where Hanford employee injuries are concerned, but the number
      of workers seriously affected by vapor inhalation appears to run in the
      hundreds since environmental cleanup began.
    </p>

    <p>
      Other categories of workers have been exposed to airborne flakes of
      plutonium, which can lodge inside the body and cause lung, kidney and bone
      cancers. Thirty-one workers tested positive for plutonium inhalation after
      an incident that occurred during the demolition of the Plutonium Finishing
      Plant on June 8, 2017. In addition to entering their lungs, the
      microscopic flakes also accumulated on their clothes and even made it into
      their cars, carrying the contamination back to their homes and families.
    </p>

    <img src="/images/PFPworkers.jpg" class="fullwidth" />
    <div class="caption">Demolition of Plutionium Finishing Plant</div>

    <p>
      Workers and their advocates have pushed back. The Energy Employees
      Occupational Illness Compensation Program Act was passed in October 2000.
      It allows DOE nuclear workers to file claims for radiogenic cancers,
      berryllium poisoning and silicosis. To date, some 53,000 workers across
      the US have been awarded payouts capped at $400,000, for a total of
      approximately $12 billion. In March 2018, Washington state legislation was
      passed to relieve Hanford workers of the burden of proving which of the
      1,500 vapor chemicals caused their illness (an impossible task in some
      cases). And in September 2018, in response to the spate of vapor
      incidents, the settlement of a Washington state lawsuit led to much more
      stringent safety measures for tank workers, including the systematic use
      of supplied air in vapor zones. These are major victories, which only
      became possible over a decade after the close of the Cold War.
    </p>

    <p>
      Local inhabitants have been a good deal less fortunate, such as the
      Hanford "downwinders" exposed to iodine-129 and other radioactive
      particles during the war. The immense majority of their cases (over three
      thousand) were rejected in 2015 after two decades of grueling litigation.
      DOE reportedly paid out a total of $60 million in legal fees, plus $7
      million in compensation. And the official literature still tells you that
      nuclear reactors are no more dangerous than the cosmic rays absorbed
      during a flight across North America. The science of denial has been
      pushed to such extremes that all knowledge about the health effects of
      radioactive materials becomes dubious.
    </p>

    <p>
      Hanford is about the slow movement of toxic compounds through bodies,
      whether of soil, groundwater, river life or human flesh. Do the bodies in
      power even care? In 2018 the Department of Energy announced plans to to
      reclassify some of the solidified tank sludge as low-level waste, meaning
      that it could simply be covered in a specialized concrete, known as
      "grout" in cleanup jargon. The tank farms would then become de facto
      nuclear graveyards&mdash;abandoned for eternity, not thousands of feet
      underground in a natural chasm or a salt mine, but just a few feet below
      the surface.
    </p>
